/* For now… */

:root {
  background: url('../images/multicolor_wash.jpg') repeat-x scroll;
  background-size: auto 10em;
  padding-top: 10em;
  fill: currentColor;
}

body {
  margin: 0 auto;
  max-width: 50em;
}

nav {
  border: 1px solid;
  border-width: 1px 0;
  margin: 1em -0.5em 3em;
  padding: 1em 0;

  a {
    display: inline-block;
    padding: 0 0.5em;
  }
}

aside {
  font-size: 1.5em;
  font-style: italic;
  padding: 1em;
}

footer {
  border-top: 1px solid;
  margin: 3em -0.5em;
  padding: 1em 0.5em;
}
